<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="8">
        <div class="title">NOTE:</div>
        <div class="body-1">
          Please select a value between
          <template v-if="method == 4">
            <span class="font-weight-bold">
              ${{ numberWithCommas(this.data.min_amt_increase) }}
            </span>
          </template>
          <template v-else>
            <span class="font-weight-bold">
              ${{ numberWithCommas(data.min_amt_reduce) }}
            </span>
          </template>
          and
          <template v-if="method == 4">
            <span class="font-weight-bold">
              ${{ numberWithCommas(data.max_amt_increase) }}
            </span>
          </template>
          <template v-else>
            <span class="font-weight-bold">
              ${{ numberWithCommas(data.max_amt_reduce) }}
            </span>
          </template>
          <v-menu right offset-x>
            <template v-slot:activator="{ on }">
              <v-btn icon v-on="on">
                <v-icon small>far fa-info-circle</v-icon>
              </v-btn>
            </template>

            <v-card max-width="350">
              <v-card-text class="body-1">
                This range is based on an industry standard formula
                incorporating your current annual election, claims reimbursed,
                pending claim reimbursements and your employer's minimum and
                maximum annual election amounts.
              </v-card-text>
            </v-card>
          </v-menu>
        </div>
      </v-col>
    </v-row>
    <v-row class="pa-0">
      <v-col cols="12" md="8">
        <v-text-field
          label="Enter your revised FSA annual election"
          outlined
          prefix="$"
          @keydown.enter="stepT"
          :rules="[rulesMeth]"
          type="number"
          v-model="newFSAval"
        ></v-text-field>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: "theUpdate",
  methods: {
    stepT() {
      if (
        Number(this.newFSAval) >= Number(this.data.annual_election_calulated) &&
        Number(this.newFSAval) <= Number(this.data.max_amt_increase)
      ) {
        this.$emit("entered");
      }
    },
    rulesMeth: function() {
      if (this.method == 3) {
        if (Number(this.newFSAval) < Number(this.data.min_amt_reduce)) {
          return (
            "You must elect at least $" +
            this.numberWithCommas(this.data.min_amt_reduce)
          );
        } else if (Number(this.newFSAval) > Number(this.data.max_amt_reduce)) {
          return (
            "You cannot exceed $" +
            this.numberWithCommas(this.data.max_amt_reduce)
          );
        } else {
          return true;
        }
      } else if (this.method == 4) {
        if (Number(this.newFSAval) < Number(this.data.min_amt_increase)) {
          return (
            "You must elect at least $" +
            this.numberWithCommas(this.data.min_amt_increase)
          );
        } else if (
          Number(this.newFSAval) > Number(this.data.max_amt_increase)
        ) {
          return (
            "You cannot exceed $" +
            this.numberWithCommas(this.data.max_amt_increase)
          );
        } else {
          return true;
        }
      }
    },
    numberWithCommas: function(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }
  },
  watch: {
    newFSAval: function() {
      this.$emit("enteredval", this.newFSAval);
    }
  },

  data: () => ({
    newFSAval: null,
    maxVal: null
  }),

  props: ["data", "method"]
};
</script>
