<template>
  <v-container fluid>
    <v-item-group v-model="selFSA">
      <v-row>
        <template v-if="currentFSA == 0 && fullData.can_add == 'T'">
          <v-col cols="12" md>
            <v-item v-slot:default="{ active, toggle }" value="1">
              <v-card
                :color="active ? 'green accent-4' : ''"
                :dark="active"
                height="200"
                @click="toggle"
              >
                <div
                  class="pa-1 headline font-weight-light text-center"
                  style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
                >
                  Start New FSA
                </div>
                <v-scroll-y-transition>
                  <div v-if="active" class="display-3 text-right px-3">
                    <v-icon large>fal fa-check</v-icon>
                  </div>
                </v-scroll-y-transition>
              </v-card>
            </v-item>
          </v-col>
        </template>
        <template v-else-if="currentFSA != 0 && fullData.can_drop == 'T'">
          <v-col cols="12" md>
            <v-item v-slot:default="{ active, toggle }" value="2">
              <v-card
                :color="active ? 'red accent-4' : ''"
                :dark="active"
                height="200"
                @click="toggle"
              >
                <div
                  class="pa-1 headline font-weight-light text-center"
                  style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
                >
                  <div>STOP</div>
                  <div>FSA Participation</div>
                </div>
                <v-scroll-y-transition>
                  <div v-if="active" class="display-3 text-right px-3">
                    <v-icon x-large>fal fa-check</v-icon>
                  </div>
                </v-scroll-y-transition>
              </v-card>
            </v-item>
          </v-col>
        </template>
        <template v-if="fullData.can_go_down == 'T'">
          <v-col cols="12" md>
            <v-item v-slot:default="{ active, toggle }" value="3">
              <v-card
                :color="active ? 'primary' : ''"
                :dark="active"
                height="200"
                @click="toggle"
              >
                <div
                  class="pa-1 headline font-weight-light text-center"
                  style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
                >
                  <div>REDUCE</div>
                  <div>Annual Election</div>
                </div>
                <v-scroll-y-transition>
                  <div v-if="active" class="display-3 text-right px-3">
                    <v-icon x-large>fal fa-check</v-icon>
                  </div>
                </v-scroll-y-transition>
              </v-card>
            </v-item>
          </v-col>
        </template>
        <template v-if="fullData.can_go_up == 'T'">
          <v-col cols="12" md>
            <v-item v-slot:default="{ active, toggle }" value="4">
              <v-card
                :color="active ? 'primary' : ''"
                :dark="active"
                height="200"
                @click="toggle"
              >
                <div
                  class="pa-3 headline font-weight-light text-center"
                  style="position:absolute;top:50%;left:8px;right:8px;transform:translateY(-50%)"
                >
                  <div>INCREASE</div>
                  <div>Annual Election</div>
                </div>
                <v-scroll-y-transition>
                  <div v-if="active" class="display-3 text-right px-3">
                    <v-icon x-large>fal fa-check</v-icon>
                  </div>
                </v-scroll-y-transition>
              </v-card>
            </v-item>
          </v-col>
        </template>
      </v-row>
    </v-item-group>
  </v-container>
</template>
<script>
export default {
  name: "theselection",
  data() {
    return {
      e1: 1,
      selFSA: null
    };
  },
  props: ["currentFSA", "fullData"],
  created: function() {
    this.$emit("selected", this.selectedMod);
    this.$emit("selectedfsa", this.selFSA);
  },
  watch: {
    selFSA: function() {
      this.$emit("selected", this.selectedMod);
      this.$emit("selectedfsa", this.selFSA);
    }
  },
  computed: {
    selectedMod() {
      switch (this.selFSA) {
        case "1":
          return "Start New FSA";

        case "2":
          return "Stop FSA Participation";

        case "3":
          return "REDUCE Annual Election";

        case "4":
          return "Increase Annual Election";

        default:
          return "Click a button below...";
      }
    }
  }
};
</script>
